import '@telerik/kendo-theme-default/styles/packages/buttons';
import {
  Directive,
  ElementRef,
  HostBinding,
  HostListener,
  Input,
  Renderer,
  OnDestroy,
  Optional
} from '@angular/core';
import { KendoButtonService } from './button.service';

/**
 * Represents the Kendo UI Button component for Angular.
 */
@Directive({
  selector: '[kendoButton]'
})
export class ButtonDirective implements OnDestroy {
  /**
   * Provides visual styling indicating if the Button is active.
   * By default, `togglable` is set to `false`.
   */
  @Input() public togglable: boolean = false;

  /**
   * Adds visual weight to the Button and makes it primary.
   */
  @Input() public primary: boolean = false;
  public element: HTMLElement;
  public renderer: Renderer;
  public isDisabled: boolean = false;
  public isIcon: boolean = false;
  public imageNode: HTMLImageElement;
  public iconNode: HTMLElement;

  /**
   * Defines a name of an existing icon in the Kendo UI theme.
   * The icon is rendered by a `span.k-icon` element inside the Button.
   */
  @Input() public set icon(icon: string) {
    this.iconSetter(icon, () => {
      this.isIcon = true;
      const classes: string = 'k-icon k-i-' + icon;
      this.addIcon(classes);
    });
  }

  /**
   * Sets the selected state of the Button.
   */
  @Input() public selected: boolean = false;

  /**
   * Defines a CSS class&mdash;or multiple classes separated by spaces&mdash;
   * which are applied to a `span` element inside the Button. Allows the use of custom icons.
   */
  @Input() public set iconClass(iconClassName: string) {
    this.iconSetter(iconClassName, this.addIcon.bind(this));
  }

  /**
   * Defines a URL, which is used for an `img` element inside the Button.
   * The URL can be relative or absolute. If relative, it is evaluated with relation to the web page URL.
   */
  @Input() public set imageUrl(imageUrl: string) {
    this.iconSetter(imageUrl, this.addImgIcon.bind(this));
  }
  /**
   * Disables the Button if set to `true`.
   */
  @Input() public set disabled(disabled: boolean) {
    this.isDisabled = disabled;
    this.renderer.setElementProperty(this.element, 'disabled', disabled);
  }

  @HostBinding('class') get className(): string {
    const textContent: number = this.element.innerText.length;
    return this.resolveClasses({
      'k-button': true,
      'k-state-disabled': this.isDisabled,
      'k-primary': this.primary,
      'k-state-active': this.selected,
      'k-button-icontext': this.isIcon && textContent,
      'k-button-icon': this.isIcon && !textContent
    });
  }

  @HostListener('click') protected onClick(): void {
    if (!this.disabled && this.service) {
      this.service.click(this);
    }

    if (!this.service) {
      this.selected = !this.selected;
    }
  }


  constructor(element: ElementRef, renderer: Renderer, @Optional() private service: KendoButtonService) {
    this.element = element.nativeElement;
    this.renderer = renderer;
  }

  public ngOnDestroy(): void {
    this.removeNode();
  }

  private resolveClasses(classes: Object): string {
    let resolved: string = '';
    Object.keys(classes).forEach(key => {
      resolved += classes[key] ? key + ' ' : '';
    });
    return resolved;
  }

  private addImgIcon(imageUrl: string): void {
    this.imageNode = this.imageNode || document.createElement('img');
    this.imageNode.src = imageUrl;
    this.imageNode.className = 'k-image';
    this.element.insertBefore(this.imageNode, this.element.firstChild);
  }

  private addIcon(classNames: string): void {
    if (this.iconNode) {
      this.iconNode.className = classNames;
    } else {
      this.iconNode = document.createElement('span');
      this.iconNode.className = classNames;
      this.element.insertBefore(this.iconNode, this.element.firstChild);
    }
  }

  private iconSetter(icon: string, insertIcon: Function): void {
    if (icon === undefined) {
      this.removeNode();
    } else {
      insertIcon(icon);
    }
  }

  private removeNode(): void {
    if (this.imageNode) {
      this.element.removeChild(this.imageNode);
    }

    if (this.iconNode) {
      this.element.removeChild(this.iconNode);
    }
  }

}



